@import "common.scss";
@import "font.scss";
.launch_wrap{
	width: 100%;
    padding:.9rem 0 0;
    min-height: 100%;
}
.top_tab{
	width: 100%;
	height: .98rem;
	background: #0093dd;
	ul{
    	width: 100%;
    	height: .98rem;
    	display: -webkit-box;
	    display: -webkit-flex;
	    display: -ms-flexbox;
	    display: flex;
	    flex-direction:row;
	    flex-wrap:nowrap;
	    justify-content:space-around;
	    align-items:center;
	    li{
	    	flex: none;
	    	height: .98rem;
	    	a{
	    		@include inlineBlock(top);
	    		font-size: .32rem;
	    		height: .98rem;
	    		line-height: .94rem;
	    		color: #fff;
	    		border-bottom: .04rem solid transparent;
	    		padding:0 .1rem;
	    	}
	    }
	    li.active{
	    	a{
	    		border-bottom: .04rem solid #fff;
	    	}
	    }
    }
}
.section01{
	width: 100%;
	background: #fff;
	padding:.5rem .2rem;
	margin-bottom: .2rem;
}
.top_infos{
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	flex-direction:row;
	flex-wrap:nowrap;
	justify-content:space-between;
	align-items:center;
	.infos,.states{
		flex: none;
	}
	.infos{
		font-size: 0;
		.pic,.name{
			@include inlineBlock(middle);
		}
	}
	.pic{
		width: 1rem;
		height: 1rem;
		margin-right: .2rem;
		img{
            display: block;
            width: 1rem;
            height: 1rem;
            border-radius: 50%;
		}
	}
	.name{
       h5{
           font-size: .3rem;
           color:#333;
       }
       p{
           font-size: .24rem;
           color: #999;
           padding-top: .1rem;
       }
	}
	.states{
		display: block;
		font-size: .3rem;
		color: #f98149;
	}
}
.applies{
	width: 100%;
	background: #f5f5f5;
	margin-top: .32rem;
	a{
		display: block;
		width: 100%;
		padding: .18rem .2rem;
		font-size: 0;
		span,p{
			@include inlineBlock(middle);
		}
		span{
			width: .98rem;
			height: .98rem;
			background: #0093dd;
			text-align: center;
			line-height: .98rem;
			color: #fff;
			font-size: .5rem;
			margin-right:.2rem;
		}
		p{
			font-size: .3rem;
			color: #333;
		}
	}
}
.checkList{
	width: 100%;
	background: #f5f5f5;
	padding-left: .2rem;
	margin-top: .32rem;
	li{
		width: 100%;
		font-size: 0;
		padding-top: .35rem;
		.checkIcon,.checkInfo_wrap{
			@include inlineBlock(top);
		}
		.checkIcon{
			width: .5rem;
			padding-top: .05rem;
			i{
				font-size: .32rem;
			}
		}
		.pass{
			i{
				font-size: .35rem;
				color: #76bf55;
			}
		}
		.check{
			i{
				font-size: .35rem;
				color: #f98149;
			}
		}
		.fail{
            i{
				color: #ff606e;
			}
		}
		.checkInfo_wrap{
			width: -webkit-calc(100% - .5rem);
			width: -moz-calc(100% - .5rem);
			width: -ms-calc(100% - .5rem);
			width: -o-calc(100% - .5rem);
			width: calc(100% - .5rem);
		}
		.checkInfo{
			width: 100%;
			padding-bottom: .25rem;
			padding-right: .2rem;
			border-bottom: .01rem solid #dbdbdb;
            display: -webkit-box;
		    display: -webkit-flex;
		    display: -ms-flexbox;
		    display: flex;
		    flex-direction:row;
		    flex-wrap:nowrap;
		    justify-content:space-between;
		    align-items:flex-start;
			.name{
				font-size: .3rem;
				color: #333;
				p{
					display: block;
					font-size: .24rem;
                    color: #999;
                    padding-top: .1rem;
				}
			}
			.state_time{
				p{
					text-align: right;
				}
                 p:first-of-type{
                   font-size:.3rem;
                   color: #333;
                 }
                 p:nth-of-type(2){
                   font-size: .24rem;
                   color: #999;
                   padding-top:.1rem;
                 }
			}
		}
	}
	li:last-of-type .checkInfo{
		border-bottom: 0;
	}
}
.content_panel{
	display: none;
}